.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login__container {
    width: 300px;
    display:flex;
    height: fit-content;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
}

.login__logo{
    width: 150px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}
.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
    text-align: center;
}
.login__container > form > h5{
    margin-bottom: 5px;
}
.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
    text-align: center;
}
.login__container > p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signInButton
{
    background: #f0c12b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}
.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color:darkgray;
}